@media screen and (max-width: 960px){

    body {
        font-size: $bodyMobile;
    }

    /* Grid */

    .content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 4%;
    }

    .page-title{
        grid-column: 1 / 4;
        padding-bottom: $desktopspaceMedium;
    }

    .full-width {
        grid-column: 1 / 4;
        padding-bottom: $desktopspaceLarge;
    }

    .left-2{
        grid-column: 1 / 3;
        padding-bottom: $desktopspaceLarge;
    }

    .right-2{
        grid-column: 2 / 4;
        padding-bottom: $desktopspaceLarge;
    }

    .left-1{
        grid-column: 1 / 2;
        padding-bottom: $desktopspaceLarge;
    }

    .center-1{
        grid-column: 2 / 3;
        padding-bottom: $desktopspaceLarge;
    }

    .right-1{
        grid-column: 3 / 4;
        padding-bottom: $desktopspaceLarge;
    }
}

@media screen and (max-width: 540px){

    body {
        font-size: $bodyMobile;
    }

    h1 {
        font-size: 2em;
    }

    .page-title h1 {
        font-size: 5.2em;
        letter-spacing: 0;
      }

      .marquee span {
        font-size: 5.2em;
        padding: 0 2vw;
      }

    .site-wrapper{
        padding: 0 $mobilespaceSmall 0 $mobilespaceSmall;
    }

    .nav-right {
        display: none;
    }

        /* Grid */

        .content {
            display: inline
            }
        
            .page-title{
                padding-bottom: $mobilespaceMedium;
            }
        
            .full-width {
                padding-bottom: $mobilespaceMedium;
            }
        
            .left-2{
                padding-bottom: $mobilespaceMedium;
            }
        
            .right-2{
                padding-bottom: $mobilespaceMedium;
            }
        
            .left-1{
                padding-bottom: $mobilespaceMedium;
            }
        
            .center-1{
                padding-bottom: $mobilespaceMedium;
            }
        
            .right-1{
                padding-bottom: $mobilespaceMedium;
            }

}

@media screen and (max-width: 340px){
    .page-title h1 {
        font-size: 9.2em;
      }

}




