/* Root */

$backgroundColor: #1a1a1a;
$bodyColor: #fff;
$textMuted: #959595;
$textYellow: #FFFE4D;
$textBlue: #66E3FF ;
$textPurple: #D980FF ;
$bodyFont: "sohne" ,Arial,sans-serif,"Apple Color Emoji";

/* Desktop Styles */

$bodyDesktop: 26px; 
$desktopspaceXS: 10px;
$desktopspaceSmall: 20px;
$desktopspaceMedium: 40px;
$desktopspaceLarge: 80px;
$desktopspaceXL: 160px;

/* Tablet Styles */

$bodyTablet: 30px; 

/* Mobile Styles */

$bodyMobile: 22px; 
$mobilespaceXS: 8px;
$mobilespaceSmall: 16px;
$mobilespaceMedium: 32px;
$mobilespaceLarge: 64px;
$mobilespaceXL: 128px;

/* Global Settings */

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  font-size: $bodyDesktop;
}

.site-wrapper{
  padding: 0 $desktopspaceMedium 0 $desktopspaceMedium;
}

img{
  max-width: 100%;
  max-height: 100%;
  display: block; 
}

/* Typography */

@font-face {
  font-family: "sohne";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/soehne-web-buch.woff") format("woff"),
       url("../fonts/soehne-web-buch.woff2") format("woff2");
}

@font-face {
  font-family: "sohne";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/soehne-web-dreiviertelfett.woff") format("woff"),
       url("../fonts/soehne-web-dreiviertelfett.woff2") format("woff2");
}

h1, h2, h3, h4, p {
  font-weight: normal;
}



h1 {
  font-size: 2.4em;
  font-weight: bold;
  line-height: 1.2;
  padding-bottom: $desktopspaceSmall;
}

.page-title h1 {
  font-size: 9.2em;
  font-weight: normal;
  letter-spacing: -5px;
  line-height: 1;
  padding-bottom: $desktopspaceMedium;
}

h2 {
  font-size: 1.4em;
  line-height: 1.2;
  font-weight: bold;
  padding-bottom: $desktopspaceSmall;

}

h3{
  font-size: 1.2em;
  font-weight: bold;
}

p.paragraph-large{
  font-size: 1.3em;
  line-height: 1.2;
}

h4 {
    font-size: 1em;
    font-weight: bold;
    line-height: 1.2;
  }

p {
  font-size: 1em;
  line-height: 1.2;
}

p.meta {
  font-size: .7em;
  letter-spacing: -.1px;
  line-height: 1.3;
}

p.mutted {
color: $textMuted;
}

a {
  color: $bodyColor;
  text-decoration: none;
}

.cool-link::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background:$bodyColor;
  transition: width .3s;
}

.cool-link:hover::after {
  width: 100%;
}

.play {
  background-color: $bodyColor;
  background-image: -webkit-linear-gradient(top left, #00D1FF, #FF00E2);
  background-image: gradient(top left, #00D1FF, #FF00E2);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  border-bottom: none;
}

.play::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background:$bodyColor;
  transition: width .3s;
  background: linear-gradient($backgroundColor, $backgroundColor),  
              linear-gradient(to right, #00D1FF, #FF00E2);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border-bottom: 2px solid transparent;
}

.play:hover::after {
  width: 100%;
  //transition: width .3s;
}

/* Navigation */

nav {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 30px 0 30px 0;
  font-size: 1.3em;
  line-height: 1.2;
}

.nav-title-open {
  width: 470px;
}

.nav-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.nav-play {
  margin-left: 1em;
}

.nav-menu-open{
  margin-left: 1em;
}

/* Index-Ticker */

.index-marquee{
  width: 100vw;
  position: fixed;
  bottom: 40px;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.marquee {
  position: relative;
  overflow: hidden;
}

.marquee__inner {
  width: fit-content;
  display: flex;
  position: relative;
  transform: translate3d(0, 0, 0);
  animation: marquee 16s linear infinite;
  animation-play-state: running;
}

.marquee span {
  font-size: 9.6em;
  padding: 0 2vw;
}

// .marquee:hover .marquee__inner {
//   animation-play-state: paused;
// }

@keyframes marquee {
  0% {
      transform: translate3d(0, 0, 0);
  }

  100% {
      transform: translate3d(-25%, 0, 0);
  }
}

/* Grid */

.content {
  display: grid;
  grid-template-columns: 80px 1fr 40px 1fr 40px 1fr 80px;
}

.page-title{
  grid-column-start: 1;
  grid-column-end: 8;
  padding-bottom: $desktopspaceMedium;
}

.full-width {
  grid-column-start: 1;
  grid-column-end: 8;
  padding-bottom: $desktopspaceLarge;
}

.left-2{
  grid-column-start: 2;
  grid-column-end: 5;
  padding-bottom: $desktopspaceLarge;
}

.right-2{
  grid-column-start: 4;
  grid-column-end: 7;
  padding-bottom: $desktopspaceLarge;
}

.left-1{
  grid-column-start: 2;
  grid-column-end: 3;
  padding-bottom: $desktopspaceLarge;
}

.center-1{
  grid-column-start: 4;
  grid-column-end: 5;
  padding-bottom: $desktopspaceLarge;
}

.right-1{
  grid-column-start: 6;
  grid-column-end: 7;
  padding-bottom: $desktopspaceLarge;
}

/* Content */

.play-h1 {
  background-color: $bodyColor;
  max-width: 460px;
  background-image: -webkit-linear-gradient(top left, #00D1FF, #FF00E2);
  background-image: gradient(top left, #00D1FF, #FF00E2);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

/* Project Block Styling */

.project-wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.project-image{
  padding-bottom: $desktopspaceXS;
}

.project-date{
  color: $textMuted;
  padding-bottom: $desktopspaceXS;
}

.project-tag{
  padding-top: $desktopspaceXS;
  display: flex;
  font-size: .6em;
  text-decoration: none;
  font-style: normal;
}

.project-tag-casestudy{
  padding-right: $desktopspaceXS;
  color: $textYellow;
}

.project-tag-inprocess{
  padding-right: $desktopspaceXS;
  color: $textBlue;
}

.project-tag-blog{
  padding-right: $desktopspaceXS;
  color: $textPurple; 
}

/* Case Study Styling */



.case-study-hero-image {
  grid-column-start: 1;
  grid-column-end: 8;  
  padding: $desktopspaceXS 0 $desktopspaceLarge 0 ;
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.case-study-image-full-width {
  grid-column-start: 1;
  grid-column-end: 8;  
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

